.chatbot-icon {
    position: fixed;
    bottom: 20px; /* Position it at the bottom */
    right: 20px; /* Position it at the right */
    cursor: pointer;
    z-index: 1000; /* Ensure it's above other elements */
}

.chatbot-icon img {
    width: 70px; /* Adjust icon size */
    height: 60px;
}
